/* eslint-disable no-nested-ternary */
/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { push } from 'react-router-redux';

import './styles.css';

import agent from '../../../agent';
import NavBar from './NavBar';

import { store } from '../../../store';
import ToolTip from '../../../common/Components/ToolTip';
import { APP_NAME } from '../../../constants/otherConstants';
import CollapseTransition from '../../../common/Components/CollapseTransition';
import PricingTable from './PricingTable';
import tick from '../../../assets/Images/LandingPage/tick.png';
import cross from '../../../assets/Images/LandingPage/cross.png';

class Pricing extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      mobileView: false,
    };
    this.pricing = [{
      heading: 'Your Links',
      features: [
        {
          featureName: 'Unlimited Links', featureDetails: 'Get unlimited link', pro: 'tick', regular: 'tick',
        },
        {
          featureName: 'Link Priority', featureDetails: 'Prioritize Your Links', pro: 'tick', regular: null,
        },
        {
          featureName: 'Link icons', featureDetails: 'Add Link icons', pro: 'tick', regular: null,
        },
        {
          featureName: 'NewsLetter ', featureDetails: 'NewsLetter and Email Sign up', pro: 'tick', regular: null,
        },
      ],

    },
    {
      heading: 'Analytics',
      features: [
        {
          featureName: 'Link traffic', featureDetails: 'Know how much traffic your Linktree gets.', pro: 'Daily,Weekly,Totals', regular: 'Totals Only',
        },
        {
          featureName: 'Link Clicks', featureDetails: 'View which links get clicked and when.', pro: 'Daily,Weekly,Totals', regular: 'Totals Only',
        },
        {
          featureName: 'Click Rates', featureDetails: 'Understand how links are performing with click through rates.', pro: 'Daily,Weekly,Totals', regular: 'Totals Only',
        },
      ],
    },
    {
      heading: 'Customization',
      features: [{
        featureName: 'Change App logo', featureDetails: `Change ${APP_NAME} Logo`, pro: 'tick', regular: null,
      },
      {
        featureName: 'Assign your own header', featureDetails: 'Assign your company name', pro: 'tick', regular: null,
      },
      {
        featureName: 'Customize Page styling', featureDetails: ' Change the Screen styling   ', pro: 'tick', regular: null,
      },
      ],
    },
    {
      heading: 'Integration',
      features: [{
        featureName: 'Integration with Facebook,Instagram,Google Sheets,YouTube,Patreon and many more', featureDetails: 'Integration with', pro: 'tick', regular: 'tick',
      },
      ],
    },
    {
      heading: 'Support',
      features: [{
        featureName: 'Support', featureDetails: 'Our Valuable Support', pro: 'tick', regular: null,
      },
      {
        featureName: 'Priority Support', featureDetails: 'You will be given first priority in Support', pro: 'tick', regular: null,
      },
      {
        featureName: 'Team Access ', featureDetails: 'Access for the whole team', pro: 'tick', regular: null,
      },
      ],
    },
    ];
  }

  componentWillMount() {
    window.addEventListener('resize', this.resize.bind(this));
    this.resize();
  }

  resize() {
    this.setState({ mobileView: window.innerWidth <= 600 });
  }

  renderPricing() {
    return (
      <div className="pricing-area">
        <div className="container">
          <div className="flex">
            <div className="single-price w-50 black-bg">
              <div className="deal-top div-1">
                <h3>Our Pricing</h3>
              </div>
              <div className="deal-bottom black-">
                <ul className="deal-item">
                  {
                      this.pricing.map((p, i) => (
                        p.features.map((f, i) => (
                          <li className="b white">
                            {f.featureName}
                          </li>
                        ))
                      ))
                    }
                </ul>
                <div className="btn-area black-bg">
                  <a href="#" className="div-1 purchase-btn" style={{ opacity: 0, pointerEvents: 'none' }}>Sign up</a>
                </div>
              </div>

            </div>
            <div className="single-price w-25">
              <div className="deal-top div-2">
                <h3>Pro Pack</h3>
                <h4>
                   Coming Soon
                </h4>
                {/* <h4>
                    5000 Rs./year
                  
                </h4> */}
                {/* <a  className=" top-btn"  >Coming Soon</a> */}
              </div>
              <div className="deal-bottom">
                <ul className="deal-item">
                  {
                      this.pricing.map((p, i) => (
                        p.features.map((k, i) => (
                          k.pro === null ? (
                            <li>
                              {' '}
                              <img src={cross} className="tick-img" />
                            </li>
                          ) : (
                            k.pro === 'tick' ? (
                              <li>
                                <img src={tick} className="tick-img" alt="" />
                              </li>
                            ) : <li className="b">{`${k.pro}`}</li>
                          )
                        ))

                      ))
                    }
                </ul>
                <div className="btn-area">
                  <a  className="div-2 purchase-btn"   >Coming Soon</a>
                </div>
              </div>

            </div>


            <div className="single-price w-25">
              <div className="deal-top div-3">
                <h3>Free</h3>
                <h4>
                      0 Rs. for life
                </h4>
                <a  className=" top-btn" onClick={() => store.dispatch(push('/signup'))} >Sign up</a>

              </div>
              <div className="deal-bottom">
                <ul className="deal-item">
                  {
                      this.pricing.map((p, i) => (
                        p.features.map((k, i) => (
                          k.regular === null
                            ? (
                              <li>
                                <img src={cross} className="tick-img" alt="" />
                              </li>
                            )
                            : (
                              k.regular === 'tick' ? (
                                <li>
                                  <img src={tick} className="tick-img" alt="" />
                                </li>
                              ) : <li className="b">{`${k.regular}`}</li>
                            )
                        ))

                      ))
                    }

                </ul>
                <div className="btn-area">
                  <a hred="#" className="div-3 purchase-btn"  onClick={() => store.dispatch(push('/signup'))}>Sign up</a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  renderMobilePricing() {
    return (
      <div className="pricing-area ">
        <div className="container">
          <div className="">
            <div className="single-price ">
              <div className="deal-top div-2">
                <h3>Pro Pack</h3>
                <h4>
                    Coming Soon
                  
                </h4>
                
                {/* <a  className=" purchase-btn"  onClick={() => store.dispatch(push('/signup'))}>Sign up</a> */}
              </div>
              <div className="deal-bottom">
                <ul className="deal-item">
                  {
                      this.pricing.map((p, i) => (
                        p.features.map((k, i) => (
                          k.pro === null ? (
                            null
                          ) : (
                            k.pro === 'tick' ? (
                              <li>
                                {k.featureName}
                              </li>
                            ) : <li className="b">{`${k.featureName}(${k.pro})`}</li>
                          )
                        ))

                      ))
                    }
                </ul>
                <div className="btn-area">
                  <a  className="div-2 purchase-btn"  >Coming Soon</a>
                </div>
              </div>
            </div>
            <div className="single-price ">
              <div className="deal-top div-3">
                <h3>Free</h3>
                <h4>
                      0 Rs. for life
                  
                </h4>
                <a  className=" top-btn" onClick={() => store.dispatch(push('/signup'))} >Sign up</a>

              </div>
              <div className="deal-bottom">
                <ul className="deal-item">
                  {
                      this.pricing.map((p, i) => (
                        p.features.map((k, i) => (
                          k.regular === null
                            ? (
                              null
                            )
                            : (
                              k.regular === 'tick' ? (
                                <li>
                                  {k.featureName}
                                </li>
                              ) : <li className="b">{`${k.featureName}(${k.regular})`}</li>
                            )
                        ))

                      ))
                    }

                </ul>
                <div className="btn-area">
                  <a  className="div-3 purchase-btn"  onClick={() => store.dispatch(push('/signup'))}>Sign up</a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  render() {
    return (
      <div className="pricing-bg"  >
        
        <div className="main-div-width pb5" id='pricing'>
          <div className="safe-home-div">
            <div className="tc center white home-page-header flex flex-column items-center">
              
              <h1 className='tc' >
                 Pick you plan or start free
              </h1>
            </div>
            {
              this.state.mobileView ? this.renderMobilePricing()
                : (
                  <div className=" center ">
                    {
                      this.renderPricing()
                    }
                  </div>
                )
            }
          </div>

        </div>
      </div>
    );
  }
}


const mapStateToProps = (state) => ({

});

const mapDispatchToProps = (dispatch) => ({

});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Pricing);
